.slider_wrapper {
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
    height: 415px;
	position: relative;
    border-radius: 25px;
}

.slider_picture {
    position: absolute;
	width: 100%;
	height: 100%;
	object-fit: cover;
    border-radius: 25px;
}

.slider_arrow {
    position: absolute;
	cursor: pointer;
}

.slider_arrowLeft {
    left: 24px;
}

.slider_arrowRight {
    right: 24px;
}

/* ** Mobile ** */

@media (max-width:767px) {

	.slider_wrapper {
		height: 255px;
		border-radius: 10px;
	}
	
	.slider_picture {
		border-radius: 10px;
	}
	
	.slider_arrowLeft {
		width: 3%;
		left: 6px;
	}
	
	.slider_arrowRight {
		width: 3%;
		right: 6px;
	}
}